import React from 'react';
import arrow_1 from './arrow_1.png';
import arrow_2 from './arrow_2.png';
import cloudly from './wi-cloud.svg';
import Item from './item.js';
import Item_1 from './item2.js';
import Item_2 from './item3.js';
import Item_3 from './item4.js';
import class_item from './item.module.css';
import class_item1 from './item_1.module.css';
import class_item2 from './item_2.module.css';
import class_item3 from './item_3.module.css';
let class_slider= {
'Slider':'slide_Slider__3eX98',
'Left':'slide_Left__1Ugu_',
'Right':'slide_Right__1r4Z4',
'Container':'slide_contaner__2doly',
'Arrow1':'slide_Arrow1__1U2EN',
'Arrow2':'slide_Arrow2__1n5Uf'
}
const Slide = () => {
	return (
		<div> 
			<div className={class_slider.Left}>
				<div className ={class_slider.Arrow1}>
					<img src={arrow_1}/>
				</div>
			</div>
			<div className={class_slider.Slider}>
				<div className = {class_slider.Container}>
					<Item />
					<Item_1 />
					<Item_2 />
					<Item_3 />
				</div>
			</div>	
			<div className ={class_slider.Right}>
				<div className ={class_slider.Arrow2}>
					<img src={arrow_2}/>
				</div>
			</div>
		</div>
	);
}
export default Slide;