import React from 'react';
import cloudly from './wi-cloud.svg';
import sunny from './wi-sun.svg';
import rainy from './wi-rain.svg';
let class_item ={
'Item':'item_Item__B-O8p',
'Sub':'item_Sub__10sLJ'
}
var now = new Date();
var dayNames = new Array("Воскресенье","Понедельник","Вторник","Среда","Четверг","Пятница","Суббота");

var now = new Date();
var textout;
var month = now.getMonth();
var date = now.getDate();
textout = date;
if (month==0) textout+=" января";
if (month==1) textout+=" февраля";
if (month==2) textout+=" марта";
if (month==3) textout+=" апреля";
if (month==4) textout+=" мая";
if (month==5) textout+=" июня";
if (month==6) textout+=" июля";
if (month==7) textout+=" августа";
if (month==8) textout+=" сентября";
if (month==9) textout+=" октября";
if (month==10) textout+=" ноября";
if (month==11) textout+=" декабря";
var day =  (textout);
const Item = () => {
	return (
	<div className={class_item.Item}> 
		<span>Сегодня</span>
		<br></br>
		<h3>{day}</h3>
		<br></br>
		<img src ={cloudly}/>
		<br></br>
		<h4> днем +26<sup>o</sup></h4>
		<h5> ночью +19<sup>o</sup></h5>
		<div className={class_item.Sub}>
			<center><h6>облачно,</h6></center>
			<center><h6>без осадков</h6></center>
		</div>
	</div>	
	);
}
export default Item;